import MuiSelect from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/FormControl';
import FilledInput from '@material-ui/core/FilledInput'
import OutlinedInput from '@material-ui/core/OutlinedInput'

function Select(props) {
    const items = props.items || [];

    let input;
    if(props.variant === "filled") {
        input = <FilledInput/>
    }
    else if(props.variant === "outlined") {
        input = <OutlinedInput/>
    }

    return <FormControl style={props.style} variant="outlined">
        {props.label ? <InputLabel style={props.style}>{props.label}</InputLabel> : null}
        <MuiSelect {...props} input={input}>
            {items.map(item => <MenuItem value={item.value}>{item.label}</MenuItem>)}
        </MuiSelect>
    </FormControl>
}

export default {
    name: 'Mui Select',
    getReactComponent() {
        return Select;
    },
    inputProps: {
        autoWidth: {type: 'boolean'},
        displayEmpty: {type: 'boolean'},
        iconComponent: {type: 'node'},
        open:  {type: 'boolean'},
        value: {type: 'string'},
        label: {type: 'string'},
        items: {type: 'array'},
        variant: {
            type: {
                name:'enum',
                enums: [{label: 'Standard',value:'standard'},{label:'Outlined',value:'outlined'},{label:'Filled',value:'filled'}]
            },
            default: 'standard'
        }
    },
    outputProps: {
        onClose: {type: 'signal'},
        onOpen: {type: 'signal'},
        onChange: {
            displayName: 'Value',
            type: 'string',
            getValue(event) {
                return event.target.value;
            }
        }
    }
};
