function addInputProps(definition, props) {
    if(!definition.inputProps) definition.inputProps = {};
    for(const name in props) {
        definition.inputProps[name] = props[name];
    }
}

function addOutputProps(definition, props) {
    if(!definition.outputProps) definition.outputProps = {};
    for(const name in props) {
        definition.outputProps[name] = props[name];
    }
}

export default {
    addPaper(definition, propPath) {
        addInputProps(definition, {
            elevation: {propPath, type: 'number', default: 2},
            square: {propPath, type: 'boolean'}
        });
    },
    addButtonBase(definition, propPath) {
        addInputProps(definition, {
            disabled: { propPath, type: 'boolean' },
            disableRipple: { propPath, type: 'boolean' },
            centerRipple: { propPath, type: 'boolean' },
        })
    }
};