import IconButton from "@material-ui/core/IconButton";
import Icon from '@material-ui/core/Icon';

function IconButtonWrapper(props) {
    return <IconButton {...props.buttonProps} style={props.style} color={'inherit'}>
        <Icon {...props.iconProps}>{props.icon}</Icon>
    </IconButton>
}

export default {
    name: 'Mui Icon Button',
    getReactComponent() {
        return IconButtonWrapper;
    },
    inputCss: {
        width: {
            displayName: 'Width',
            type: {
                name: 'number',
                units: ['px', '%'],
                defaultUnit: 'px'
            }
        },
        height: {
            displayName: 'Height',
            type: {
                name: 'number',
                units: ['px', '%'],
                defaultUnit: 'px'
            }
        }
    },
    inputProps: {
        icon: { type: 'string', default: 'star' },
        color: {
            propPath: 'iconProps',
            type: {
                name: 'enum',
                enums: [
                    {label: 'Primary', value: 'primary' },
                    {label: 'Inherit', value: 'inherit'},
                    {label: 'Secondary', value: 'secondary'},
                    {label: 'Action', value: 'action'},
                    {label: 'Error', value: 'error'},
                    {label: 'Disabled', value: 'disabled'},
                ]
            },
            default: 'inherit'
        },
        fontSize: {
            displayName: 'Size',
            propPath: 'iconProps',
            type: {
                name: 'enum',
                enums: [
                    {label: 'Default', value: 'default'},
                    {label: 'Small', value: 'small'},
                    {label: 'Large', value: 'large'},
                ]
            },
            default: 'default'
        },
        disabled: { propPath: 'buttonProps', type: 'boolean' },
        disableRipple: { propPath: 'buttonProps', type: 'boolean' },
        centerRipple: { propPath: 'buttonProps', type: 'boolean' },
    },
    outputProps: {
        onClick: {
            propPath: 'buttonProps',
            type: 'signal',
            displayName: 'Click'
        }
    }
};