import MuiTextField from '@material-ui/core/TextField';

export default {
    name: 'Mui TextField',
    getReactComponent() {
        return MuiTextField;
    },
    inputProps: {
        autoComplete: {type: 'string'},
        autoFocus: {type: 'boolean'},
        defaultValue: {type: 'string'},
        disabled: {type: 'boolean'},
        error: {type: 'boolean'},
        fullWidth: {type: 'boolean'},
        helperText: {type:'node'},
        label: {type: 'string'},
        id: {type: 'string'},
        margin: {
            type: {
                name:'enum',
                enums: [{label: 'Dense',value:'dense'},{label:'None',value:'none'}]
            }
        },
        multiline: {type: 'boolean'},
        name: {type: 'string'},
        placeholder: {type: 'string'},
        readOnly: {type: 'boolean'},
        required: {type: 'boolean'},
        rows: {type: 'string'},
        rowsMax: {type: 'string'},
        type: {type:'string'},
        value: {type: 'string', default: ''},
        variant: {
            type: {
                name:'enum',
                enums: [{label: 'Standard',value:'standard'},{label:'Outlined',value:'outlined'},{label:'Filled',value:'filled'}]
            },
            default: 'standard'
        },
    },
    outputProps: {
        onChange: {
            displayName: 'Value',
            type: 'string',
            getValue(event) {
                this.sendSignalOnOutput('valueChanged');
                return event.target.value;
            }
        }
    },
    outputs: {
        valueChanged: {type: 'signal'}
    }
};
