import MuiTypography from '@material-ui/core/Typography';

function TypographyWrapper(props) {
    //wrap children in a <span> instead of the default <p>
    //to support Noodl's Text Node that uses a <div>
    // (<p> aren't allowed by the standard to have a <div> as a child
    // and React complains)
    return <MuiTypography component='span' {...props}/>
}

export default {
    name: 'Mui Typography',
    getReactComponent() {
        return TypographyWrapper;
    },
    inputProps: {
        align: {
            type: {
                name: 'enum',
                enums: [
                    {label: 'Inherit', value: 'inherit'},
                    {label: 'Left', value: 'left'},
                    {label: 'Center', value: 'center'},
                    {label: 'Right', value: 'right'},
                    {label: 'Justify', value: 'justify'},
                ]
            },
            default: 'inherit'
        },
        color: {
            type: {
                name: 'enum',
                enums: [
                    {label: 'Default', value: 'default' },
                    {label: 'Error', value: 'error'},
                    {label: 'Inherit', value: 'inherit'},
                    {label: 'Primary', value: 'primary' },
                    {label: 'Secondary', value: 'secondary'},
                    {label: 'Text Primary', value: 'textPrimary'},
                    {label: 'Text Secondary', value: 'textSecondary'},
                ]
            },
            default: 'default'
        },
        gutterBottom: {type: 'boolean'},
        inline: {type: 'boolean'},
        noWrap: {type: 'boolean'},
        paragraph: {type: 'boolean'},
        variant: {
            type: {
                name: 'enum',
                enums: [
                    {label: 'h1', value: 'h1'},
                    {label: 'h2', value: 'h2'},
                    {label: 'h3', value: 'h3'},
                    {label: 'h4', value: 'h4'},
                    {label: 'h5', value: 'h5'},
                    {label: 'h6', value: 'h6'},
                    {label: 'Subtitle 1', value: 'subtitle1'},
                    {label: 'Subtitle 2', value: 'subtitle2'},
                    {label: 'Body 1', value: 'body1'},
                    {label: 'Body 2', value: 'body2'},
                    {label: 'Caption', value: 'caption'},
                    {label: 'Button', value: 'button'},
                    {label: 'Overline', value: 'overline'},
                    {label: 'SrOnly', value: 'srOnly'},
                    {label: 'Inherit', value: 'inherit'},
                    {label: 'Display 1', value: 'display1'},
                    {label: 'Display 2', value: 'display2'},
                    {label: 'Display 3', value: 'display3'},
                    {label: 'Headline', value: 'headline'},
                    {label: 'Title', value: 'title'},
                    {label: 'Subheading', value: 'subheading'}
                ]
            },
            default: 'body1'
        },
    },
    outputProps: {
        onChange: {
            displayName: 'Value',
            type: 'number',
            getValue: (event, value)=>{
                return value
            },
            onChange() {
                this.sendSignalOnOutput('valueChanged');
            }
        },
        onDragStart: {displayName: 'Drag Started', type: 'signal'},
        onDragEnd: {displayName: 'Drag Ended', type: 'signal'},
    },
    outputs: {
        valueChanged: {displayName: 'Value Changed', type: 'signal'}
    }
};