import MuiTabs from "@material-ui/core/Tabs";
import MuiTab from "@material-ui/core/Tab";
import MuiBottomNavigationAction from "./bottom-navigation";
import React from "react";

export const Tabs = {
    name: 'Mui Tabs',
    getReactComponent() {
        return MuiTabs;
    },
    defaultCss: {
        flexShrink: 0
    },
    inputProps: {
        centered: {type: 'boolean', default: false},
        indicatorColor: {
            type: {
                name: 'enum',
                enums: [
                    {label: 'Primary', value: 'primary'},
                    {label: 'Secondary', value: 'secondary'},
                ]
            },
            default: 'secondary'
        },
        scrollButtons: {
            type: {
                name: 'enum',
                enums: [
                    {label: 'auto', value: 'auto'},
                    {label: 'on', value: 'on'},
                    {label: 'off', value: 'off'},
                ]
            },
            default: 'auto'
        },
        textColor: {
            type: {
                name: 'enum',
                enums: [
                    {label: 'Primary', value: 'primary'},
                    {label: 'Secondary', value: 'secondary'},
                    {label: 'Inherit', value: 'inherit'},
                ]
            },
            default: 'inherit'
        },
        value: {
            type: 'number',
            displayName: 'Selected Tab',
            default: 0
        },
        variant: {
            type: {
                name: 'enum',
                enums: [
                    {label: 'Standard', value: 'standard'},
                    {label: 'Scrollable', value: 'scrollable'},
                    {label: 'FullWidth', value: 'fullWidth'}
                ]
            },
            default: 'standard'
        }
    },
    outputProps: {
        onChange: {
            displayName: 'Tab Index',
            type: 'number',
            getValue: (event, value)=>{
                return value
            },
            onChange() {
                this.sendSignalOnOutput('tabChanged');
            }
        }
    },
    outputs: {
        tabChanged: {displayName: 'Tab Changed', type: 'signal'}
    }
};

export const Tab = {
    name: 'Mui Tab',
    allowChildren: false,
    getReactComponent() {
        return MuiTab;
    },
    inputProps: {
        disabled: {type: 'boolean', default: false},
        label: {type: 'string'},
        icon: {type: 'node'}
    }
};