import { createMuiTheme, MuiThemeProvider } from '@material-ui/core/styles';

function ThemeWrapper(props) {

    const options = {
        palette: {
        },
        typography: { useNextVariants: true }
    };
    if(props.primary) {
        options.palette.primary = {main: props.primary};
    }
    if(props.secondary) {
        options.palette.secondary = {main: props.secondary};
    }
    if(props.error) {
        options.palette.error = {main: props.error};
    }
    if(props.contrastThreshold) {
        options.palette.contrastThreshold = props.contrastThreshold;
    }
    options.palette.type = props.type;

    const theme = createMuiTheme(options);

    return <MuiThemeProvider theme={theme}>{props.children}</MuiThemeProvider>;
}

export default {
    name: 'Mui Theme',
    getReactComponent() {
        return ThemeWrapper;
    },
    inputProps: {
        primary: {
            group: 'Theme Colors',
            displayName: 'Primary',
            type: 'color'
        },
        secondary: {
            group: 'Theme Colors',
            displayName: 'Secondary',
            type: 'color'
        },
        error: {
            group: 'Theme Colors',
            displayName: 'Error',
            type: 'color'
        },
        contrastThreshold: {type: 'number'},
        type: {
            type: {
                name: 'enum',
                enums: [
                    {label: 'Light', value: 'light'},
                    {label: 'Dark', value: 'dark'},
                ],
                default: 'dark'
            }
        }
    }
}