import MuiSlider from '@material-ui/lab/Slider';

export default {
    name: 'Mui Slider',
    getReactComponent() {
        return MuiSlider;
    },
    inputProps: {
        disabled: {type: 'boolean'},
        max: {type: 'number', default: 100},
        min: {type: 'number', default: 0},
        step: {type: 'number'},
        value: {type: 'number'},
        vertical: {type: 'boolean'}
    },
    outputProps: {
        onChange: {
            displayName: 'Value',
            type: 'number',
            getValue: (event, value)=>{
                return value
            },
            onChange() {
                this.sendSignalOnOutput('valueChanged');
            }
        },
        onDragStart: {displayName: 'Drag Started', type: 'signal'},
        onDragEnd: {displayName: 'Drag Ended', type: 'signal'},
    },
    outputs: {
        valueChanged: {displayName: 'Value Changed', type: 'signal'}
    }
};