import MuiBottomNavigation from '@material-ui/core/BottomNavigation';
import MuiBottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import React from "react";

export const BottomNavigation = {
    name: 'Mui Bottom Navigation',
    getReactComponent() {
        return MuiBottomNavigation;
    },
    defaultCss: {
        flexShrink: 0
    },
    inputProps: {
        value: {type: 'string'},
        showLabels: {type: 'boolean'},
    },
    outputs: {
        tabChanged: {
            displayName: 'Tab Changed',
            type: 'signal'
        }
    },
    outputProps: {
        onChange: {
            displayName: 'Tab Index',
            type: 'number',
            getValue: (event, value)=>{
                return value
            },
            onChange() {
                this.sendSignalOnOutput('tabChanged');
            }
        }
    }
};

export const BottomNavigationAction = {
    name: 'Mui Bottom Navigation Action',
    getReactComponent() {
        return MuiBottomNavigationAction;
    },
    allowChildren: false,
    inputProps: {
        label: {type: 'string'},
        icon: {type: 'node'},
        showLabel: {type: 'boolean'},
        // value: {type: 'string'}
    }
};